import React, { useState, useEffect } from 'react';

import Button from 'react-bootstrap/Button';

import Form from 'react-bootstrap/Form';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ToastContainer, toast } from 'react-toastify';

// eslint-disable-next-line import/no-extraneous-dependencies
import 'react-toastify/dist/ReactToastify.css';

import logoAviator from '../../statics/assets/images/aviator.svg';
import Animation from '../Design/Animation';
import LeftCloud from '../Design/Clouds/LeftCloud';
import RightCloud from '../Design/Clouds/RightCloud';
import './index.scss';

function MainContent() {
  const [generatedNumber, setGeneratedNumber] = useState(null);
  const [canGenerateNumber, setCanGenerateNumber] = useState(true);
  const [nextGenerationTime, setNextGenerationTime] = useState(null);
  const [plusTwoMinutesTime, setPlusTwoMinutesTime] = useState(null);
  const showDelayedToast = (nextGenerationTime) => {
    toast.error(
      `   .
          
         ${nextGenerationTime}.`,
      {
        autoClose: 5000,
      },
    );
  };
  const generateNumber = () => {
    if (canGenerateNumber) {
      const randomNumber = Math.random();
      let number;
      toast.success('  !', { autoClose: 3000 });
      if (randomNumber < 0.7) {
        number = (Math.random() * (2 - 1.5) + 1.5).toFixed(2);
      } else if (randomNumber < 0.9) {
        number = (Math.random() * (3 - 2) + 2).toFixed(2);
      } else {
        number = (Math.random() * (5 - 3) + 3).toFixed(2);
      }

      setGeneratedNumber(number);
      setCanGenerateNumber(false);
      localStorage.setItem('generatedNumber', number);
      localStorage.setItem('lastGeneratedTime', Date.now());
      const nextTime = new Date(Date.now() + 10 * 60 * 1000);
      setNextGenerationTime(formatTime(nextTime));
      const plusTwoMinutes = new Date(Date.now() + 2 * 60 * 1000);
      setPlusTwoMinutesTime(formatTime(plusTwoMinutes));
    } else if (!canGenerateNumber) {
      showDelayedToast(nextGenerationTime);
    }
  };

  const formatTime = (time) => {
    const hours = time.getHours().toString().padStart(2, '0');
    const minutes = time.getMinutes().toString().padStart(2, '0');
    return `${hours}:${minutes}`;
  };

  useEffect(() => {
    const lastGeneratedTime = localStorage.getItem('lastGeneratedTime');
    const currentTime = Date.now();

    if (lastGeneratedTime) {
      // eslint-disable-next-line radix
      const elapsedTime = currentTime - parseInt(lastGeneratedTime);

      if (elapsedTime < 10 * 60 * 1000) {
        setCanGenerateNumber(false);

        const timer = setTimeout(() => {
          setCanGenerateNumber(true);
          setNextGenerationTime(null);
          setPlusTwoMinutesTime(null);
        }, 10 * 60 * 1000 - elapsedTime);

        const nextTime = new Date(currentTime + 10 * 60 * 1000 - elapsedTime);
        setNextGenerationTime(formatTime(nextTime));

        const plusTwoMinutes = new Date(
          currentTime + 2 * 60 * 1000 - elapsedTime,
        );
        setPlusTwoMinutesTime(formatTime(plusTwoMinutes));
        return () => clearTimeout(timer);
      }
      setGeneratedNumber(localStorage.getItem('generatedNumber'));
    }
  }, []);
  return (
    <div>
      <div className='main-content'>
        <LeftCloud />
        <RightCloud />
        <div className='main-content__cloud' />
        <div className='main-content__black-lightning' />
        <div className='main-content__white-lightning' />
        <div className='main-content__redline' />
        <div className='main-container'>
          <div className='main-container__logo-image'>
            <div className='main-container__logo-image_gray-circle'>
              {generatedNumber ? (
                <>
                  <p className='result-time'>: {plusTwoMinutesTime}</p>
                  <p className='result'>{generatedNumber}X</p>
                </>
              ) : (
                <Animation />
              )}
            </div>
          </div>
        </div>
        <div className='bottom-container'>
          <img
            src={logoAviator}
            alt='Aviator'
            className='bottom-container__logo-name'
          />
          <p className='bottom-container__main-text'>Loem poserg askd</p>
          <p className='bottom-container__bottom-text'>
            Etiam vulputate consequat nibh, eget
            <br />
            faucibus dsas das sdasdassadasdasd
          </p>
          <Button
            onClick={generateNumber}
            className='button'
            variant='outline-secondary'
          >
            
          </Button>{' '}
          <ToastContainer theme='dark' />
        </div>
      </div>
    </div>
  );
}

export default MainContent;
